body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue','Montserrat'
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* body{
  font-family: 'Montserrat', sans-serif;
} */

.navbar{
  background-image: linear-gradient(#9B85F4 10%, #7150F8 100%, #9B85F4 2%);
}

.navbar a{
  color: white;
  margin-left: 30px;
  font-weight: 700;
  font-size: 1.8rem;
}

.banner{
  background: url('../public/assets/banner.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-height: 40%;
}

.banner h1{
  color: white;
}

.banner .section{
  max-width: 800px;
}

.banner form{
  padding-top: 8%;
  padding-bottom: 10%;
}

.banner form input{
  padding: 15px;
  padding-left: auto;
  font-size: 1.2rem;
  max-width: 92%;
  margin: auto;
}

.banner form input[type="text"] {  
  -webkit-box-shadow: none;
  outline: -webkit-focus-ring-color auto 0px;
} 

.banner form .section button{
  padding: 15px;
  font-size: 1.2rem;
  max-width: 100%;
  background-color: #7150F8;
  border:1px solid #7150F8;
}

.section h1{
  padding: 10px;
  font-size: 2.4rem;
}

/* Video Section */

.video-section{
  margin-top: 5%;
  margin-bottom: 5%;
  margin-left: auto;
  margin-right: auto;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  max-width: 950px;
  padding: 30px;
}

.video-section .row{
  margin: 10px;
}

.video-section .row h5{
  border: 2px solid;
  padding: 10px;
}

.video-section button{
  width: 90%;
  margin: auto;
  height: 87%;
}

/* Guide Section */

.guide{
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  max-width: 1000px;
  padding: 20px;
  margin-top: 5%;
  margin-bottom: 5%;
}

.guide h3{
  font-weight: 700;
  padding-top:5px;
}

.guide .gif{
  padding: 30px;
}

.guide .steps{
  padding: 5%;
  text-align: left;
}

.guide .dot{
  background-color: #7150F8;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  text-align: center;
}

.guide .dot h5{
  margin-top: 8px;
  color: white;
}

.guide .step{
  margin-top: 8px;
}

.guide .row{
  margin-top: 8px;
  margin-bottom: 8px;
}

/* Footer Section */

footer{
color: white;
padding: 20px;
}

footer a{
text-decoration: none;
color: white;
}

footer a:hover{
color: white;
}

footer p{
text-align: center;
}

footer input{
padding-left: 10px;
  width: 100%;
}

footer input::placeholder{
font-size: 15px;
}

footer button{
border: 0px;
padding-left: 10px;
padding-right: 10px;
background-color: #4F52BA;
color: white;
}

footer h5{
margin-top: 20px;
}

footer h6{
font-weight: normal;
font-size: 0.8rem;
}

@media only screen and (max-width : 768px){
  .section h1 {
      font-size: 2.3rem;
  }

  .banner form input{
      max-width: 95%;
      margin: auto;
      margin-left: 5%;
  }

  .video-section h4{
      font-size: 1rem;
  }

  .guide h5{
      font-size: 0.9rem;
  }

  .guide .dot{
      height: 30px;
      width: 30px;
  }
}

@media only screen and (max-width:426px){
  .banner form .section button {
      padding: 15px;
      font-size: 1.2rem;
      max-width: 90%;
      margin: auto;
      margin-left: 5%;
  }

  .banner form input{
      max-width: 90%;
  }

  .video-section h4 {
      font-size: 1.1rem;
      margin-top: 15px;
  }

  .guide .steps{
      padding: 12px;
      padding-left: 40px;
  }
}

@media only screen and (max-width:375px){
  .section h1 {
      font-size: 2rem;
  }

  .banner form input{
      padding: 10px;
  }

  .banner form .section button{
      padding: 8px;
  }

  .video-section h5{
      font-size: 1rem;
  }

  br{
      display: none;
  }
}

@media only screen and (max-width:320px){
  .section h1 {
      font-size: 1.6rem;
  }

  .banner form input {
      padding: 7px;
      padding-left: 20px;
      font-size: 15px;
  }

  .banner form .section button {
      padding: 5px;
      font-size: 17px;
  }

  .video-section h4 {
      font-size: 0.8rem;
      margin-top: 10px;
  }

  .video-section h5 {
      font-size: 0.8rem;
  }

  .video-section button {
      width: 90%;
      margin: auto;
      font-size: 0.7rem;
      height: 87%;
  }

  .video-section{
      padding: 10px;
  }
}